import { prisma } from "./prisma.server";
import type { Forms } from "./types.server";
import { createCookieSessionStorage, json, redirect } from "@remix-run/node";
import { createUser } from "./users.server";
import bcrypt from "bcryptjs";
const token = process.env.TOKEN_SECRET;
if (!token) {
  throw new Error("TOKEN_SECRET must be set");
}

const storage = createCookieSessionStorage({
  cookie: {
    name: "pokedex-session",
    secure: process.env.NODE_ENV == "production",
    secrets: [token],
    sameSite: "lax",
    path: "/",
    maxAge: 60 * 60 * 24 * 30,
    httpOnly: true,
  },
});

//Create user
export const register = async (form: Forms) => {
  const userExist = await prisma.user.count({
    where: { username: form.username },
  });

  if (userExist) {
    return json(
      {
        error: "User already exist",
      },
      { status: 400 }
    );
  }
  const newUser = await createUser(form);

  if (!newUser) {
    return json({ error: "something went wrong" }, { status: 400 });
  }
  return createUserSession(newUser.id, "/");
};

export const login = async (form: Forms) => {
  const user = await prisma.user.findUnique({
    where: { username: form.username },
  });

  if (!user || !(await bcrypt.compare(form.password, user.password))) {
    return json({ error: "Erreur dans le formulaire de connection" });
  }
  return createUserSession(user.id, "/");
};

//create a session

export const createUserSession = async (userId: string, redirectTo: string) => {
  const session = await storage.getSession();
  session.set("userId", userId);
  return redirect(redirectTo, {
    headers: {
      "Set-Cookie": await storage.commitSession(session),
    },
  });
};

export async function requireUserId(
  request: Request,
  redirectTo: string = new URL(request.url).pathname
) {
  const session = await getUserSession(request);

  const userId = session.get("userId");

  if (!userId || typeof userId !== "string") {
    const searchParams = new URLSearchParams([["redirectTo", redirectTo]]);
    throw redirect(`/login?${searchParams}`);
  }

  return userId;
}

function getUserSession(request: Request) {
  return storage.getSession(request.headers.get("Cookie"));
}

export async function getUserId(request: Request) {
  const session = await getUserSession(request);
  const userId = session.get("userId");
  if (!userId || typeof userId !== "string") return null;
  return userId;
}

export async function getUser(request: Request) {
  const userId = await getUserId(request);
  if (typeof userId !== "string") return null;
  try {
    const user = await prisma.user.findUnique({
      where: { id: userId },
      select: { id: true, username: true, pokemonCatch: true },
    });
    return user;
  } catch {
    throw logout(request);
  }
}

export async function logout(request: Request) {
  const session = await getUserSession(request);
  return redirect("/login", {
    headers: {
      "Set-Cookie": await storage.destroySession(session),
    },
  });
}
